body {
  // font-family: 'Courier New';
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.homepage {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 80px;
    align-items: center;
    justify-content: center;
  }
  
  .directory-menu {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    overflow: hidden; // So that the image wouldn't expend beyond the div size.

    &:hover {
      cursor: pointer;

      & .background-image { // To make the image grow and expend.
        transform: scale(1.1);
        transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      }

      & .content {
        opacity:0.9;
      }
    }


    &.large {   /* If "menu-item" also has "large" then (in the directory.component.jsx)*/
        height: 380px;
    }
  
    &:first-child {
      margin-right: 7.5px;
    }
  
    &:last-child {
      margin-left: 7.5px;
    }

    .background-image {
      width: 100%;    // The image would always be the size of the container.
      height: 100%;   // The image would always be the size of the container.
      background-position: center;
      background-size: cover;
    }
  
    .content {
      height: 90px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      background-color: white;
      opacity: 0.7;
      position: absolute;
  
      .title {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #4a4a4a;
      }
  
      .subtitle {
        font-weight: lighter;
        font-size: 16px;
      }
    }
  }